.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 0;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__btn {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin: 0 auto 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  display: block;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__btn {
  color: #00acdc;
  background-color: #fff;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-digit {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-value {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 16px;
}

.stars {
  justify-content: flex-start;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 :nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__item {
  height: 60px;
  margin: auto 10px;
  list-style-type: none;
}

.nav__item:first-child, .nav__item:last-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-family: inherit;
  font-weight: 500;
  line-height: 60px;
  display: block;
}

.nav__link:hover {
  color: var(--blue-color);
  transition-duration: .3s;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding-top: 50px;
  padding-bottom: 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

:root {
  --blue-color: #00acdc;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  background-color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

a {
  color: inherit;
  text-decoration: none;
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

